    </div> -->
    <van-nav-bar            
        left-arrow
        title="追问详情"
        @click-left="onClickLeft"
    >    
    </van-nav-bar>      
    <div class="item">
      <div class="list" v-for="(item,index) in list" :key="index">
      <span class="content_time" v-show="item.dateline == item.dateline">{{item.dateline}}</span>
      <div class="content">
        <div class="right" v-show="item.right == 1">
          <span class="right_text">{{item.message}}</span>
          <!-- <img src class="right_img" alt /> -->
          <img :src='item.avatar' class="right_avatar" alt />
        </div>
        <div class="left" v-show="item.right == 0">
          <img :src='item.avatar' class="left_avatar" alt />
          <span class="left_text">{{item.message}}</span>
          <!-- <img src class="left_img" alt /> -->
        </div>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      item: {
        token: this.$store.state.userData,
        tid: this.$route.query.tid,
        pid: this.$route.query.pid
      },
      list:[]
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.api.getDialogue(this.item).then(res => {
          if(res.err_code == 0) {
              for(let i = 0;i<res.data.dialogue.length;i++) {
                  let a = this.GMTToStr(res.data.dialogue[i].dateline);
                  console.log(a)
              }
              this.list = res.data.dialogue;
          }
      })
    },
    onClickLeft(){
      this.$router.go(-1);
    },
    GMTToStr(time) {
        var date 
    if (time) {
        date = new Date(time);
    } else {
        date = new Date();
    }
    var nowYear = date.getFullYear();
    var nowMonth = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
    var nowDay = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
    return nowYear + '-' + nowMonth + '-' + nowDay;
    }
  }
};
</script>
